@font-face {
  font-family: "Cairo";
  src: local("Cairo"), url(./assets/fonts/Cairo-Regular.ttf) format("truetype");
}
@font-face {
  font-family: "Cairo-bold";
  src: local("Cairo"), url(./assets/fonts/Cairo-Bold.ttf) format("truetype");
}
@font-face {
  font-family: "Cairo-semi";
  src: local("Cairo"), url(./assets/fonts/Cairo-SemiBold.ttf) format("truetype");
}

:root {
  --primary-color: #b8656c;
  --secondary-color: #222823;
  --third-color: #ffe2d1;
  --second-primary-color: #045d7a;
  --second-secondary-color: #ff8552;
  --second-third-color: #39393a;
  --third-primary-color: #412234;
  --third-secondary-color: #996888;
  --third-third-color: #e26d5a;
  --dark-color: #8a8a8a;
  --darker-color: #3e3e3e;
  --light-color: #f4f4f4;
  --error-color: #931621;
  --warning-color: #ecd444;
  --success-color: #28a745;
}
body {
  margin: 0;
}
main {
  /* background-color: #fafafa; */
  margin-bottom: 100px;
}
/*GLOBALS*/
a {
  text-decoration: none;
  color: inherit;
}
.rtl {
  direction: rtl;
}
.ltr {
  direction: ltr;
}
/*texts*/
.cairo {
  font-family: "Cairo" !important;
}
text {
  font-family: "Cairo";
}
.center {
  text-align: center;
}
.text-left {
  text-align: left;
}
.text-right {
  text-align: right;
}
.text-end {
  text-align-last: end;
  -moz-text-align-last: end;
  -ms-text-align-last: end;
}
.text-start {
  text-align-last: start;
  -moz-text-align-last: start;
  -ms-text-align-last: start;
}
.text-primary {
  color: var(--primary-color);
}
.text-light {
  color: var(--light-color);
}
.text-error {
  color: var(--error-color);
}
.text-secondary {
  color: var(--secondary-color);
}
.text-success {
  color: var(--success-color);
}
.text-shadow-grey {
  text-shadow: 0 0 2px var(--dark-color);
}
.text-shadow-light {
  text-shadow: 0px 0px 2px var(--light-color);
}
.text-normal {
  font-size: medium;
}
.text-small {
  font-size: small;
}
.text-smaller {
  font-size: smaller;
}
.text-larger {
  font-size: larger;
}
.text-xlarge {
  font-size: x-large;
}
.text-xxlarge {
  font-size: xx-large;
}

.bold {
  /* font-weight: 600 !important; */
  font-family: "Cairo-semi" !important;
}
.bolder {
  /* font-weight: 600 !important; */
  font-family: "Cairo-bold" !important;
}
.underlined {
  text-decoration: underline;
  text-underline-position: under;
}
/* Padding */
.xp {
  padding: 0.2rem;
}
.p {
  padding: 0.5rem;
}

.p-1 {
  padding: 1rem;
}
.p-15 {
  padding: 1.5rem;
}
.p-2 {
  padding: 2rem;
}

.p-3 {
  padding: 3rem;
}

.py {
  padding: 0.5rem 0;
}

.py-1 {
  padding: 1rem 0;
}
.py-15 {
  padding: 1.5rem 0;
}
.py-2 {
  padding: 2rem 0;
}

.py-3 {
  padding: 0 3rem;
}

.px {
  padding: 0 0.5rem;
}

.px-1 {
  padding: 0 1rem;
}

.px-2 {
  padding: 0 2rem;
}

.px-3 {
  padding: 0 3rem;
}
.clearp {
  padding: 0;
}
/* Margin */
.xm {
  margin: 0.1rem;
}
.m {
  margin: 0.5rem;
}

.m-1 {
  margin: 1rem;
}

.m-2 {
  margin: 2rem;
}

.m-3 {
  margin: 3rem;
}

.my {
