}

.my-1 {
  margin: 1rem 0;
}

.my-2 {
  margin: 2rem 0;
}

.my-3 {
  margin: 3rem 0;
}

.mx {
  margin: 0 0.5rem;
}

.mx-1 {
  margin: 0 1rem;
}

.mx-2 {
  margin: 0 2rem;
}
.mx-3 {
  margin: 0 3rem;
}
.clearm {
  margin: 0;
}
.clearp {
  padding: 0;
}
.fullheight {
  height: 100%;
}
.fullheightVH {
  height: 100vh;
}
.fullheightVH80 {
  height: 80vh;
}
.fullwidth {
  width: 100%;
}
.width80 {
  width: 80%;
}
.width60 {
  width: 60%;
}
.position__centerlize {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.flex__centerlize {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.block {
  display: block;
}
.hidden {
  display: none;
}
.flex {
  display: flex;
  /* margin: auto; */
}
.center-self {
  align-self: center;
}
.baseline-self {
  align-self: baseline;
}
.flex-justify-evenly {
  justify-content: space-evenly;
}
.flex-justify-center {
  justify-content: center;
}
.flex-justify-between {
  justify-content: space-between;
}
.flex-justify-start {
  justify-content: flex-start;
}
.flex-justify-end {
  justify-content: flex-end;
}
.flex-wrap {
  flex-wrap: wrap;
}
.ver {
  flex-direction: column;
}
.hor {
  flex-direction: row;
  flex-flow: wrap;
}
.align-items-center {
  align-items: center;
}
.align-items-start {
  align-items: flex-start;
}
.align-items-end {
  align-items: flex-end;
}
.grow1 {
  flex-grow: 1;
}
.display_contents {
  display: contents;
}
.hoverable-op:hover {
  opacity: 0.8;
  cursor: pointer;
}
.hoverable-top:hover {
  transform: scale(1.05);
  transition: all 0.2s ease-in-out;
}
.hoverable-shadow-text:hover {
  text-shadow: 0px 0px 1px 1px var(--dark-color);
  cursor: pointer;
}
.hoverable-box-shadow:hover {
  box-shadow: 0px 0px 20px var(--darker-color);
  cursor: pointer;
}
.box-shadow {
  box-shadow: 0px 0px 3px var(--dark-color);
}

/*Borders*/
.border {
  border: solid 1px var(--darker-color);
  border-radius: 3px;
}
.border-light {
  border: solid 1px var(--dark-color);
  border-radius: 3px;
}
.border-lighter {
  border: solid 1px #dcdcdc;
  border-radius: 3px;
}
.border-bottom {
  border-bottom: solid 1px var(--dark-color);
}
.border-top {
  border-top: solid 1px var(--dark-color);
}
.border-right {
  border-right: solid 1px var(--dark-color);
}
.border-left {
  border-left: solid 1px var(--dark-color);
}
.dashed {
  border-style: dashed;
}
.noborder {
  border: 0;
}

.overflow_hidden {
  overflow: hidden;
}
/*BGs*/
.bg-logo-sm {
  /* background-image: url("./assets/images/logo-w.png"); */
  width: 120px;
  height: 160px;
  background-size: contain;
  background-repeat: no-repeat;
}
/*Main*/
.bg-primary-light {
  background: #f3e3e4;
}
.bg-primary {
  background-color: var(--primary-color);
}
.bg-secondary {
  background-color: var(--secondary-color);
}
.bg-primary-gradient {
  background: var(--primary-color);
  background: -moz-linear-gradient(
    300deg,
    #f3e3e4 0%,
    var(--primary-color) 100%
  );
  background: -webkit-linear-gradient(
    300deg,
    #f3e3e4 0%,
    var(--primary-color) 100%
  );
  background: linear-gradient(300deg, #f3e3e4 0%, var(--primary-color) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#F3E3E4",endColorstr=var(--primary-color),GradientType=1);
}
.bg-secondary-gradient {
  background: var(--secondary-color);
  background: -moz-linear-gradient(
    300deg,
    #303531 0%,
    var(--secondary-color) 100%
  );
  background: -webkit-linear-gradient(
    300deg,
    #303531 0%,
    var(--secondary-color) 100%
  );
  background: linear-gradient(300deg, #303531 0%, var(--secondary-color) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#303531",endColorstr=var(--secondary-color),GradientType=1);
}
.bg-third-gradient {
  background: var(--third-color);
  background: -moz-linear-gradient(300deg, #e04856 0%, var(--third-color) 100%);
  background: -webkit-linear-gradient(
    300deg,
    #e04856 0%,
    var(--third-color) 100%
  );
  background: linear-gradient(300deg, #e04856 0%, var(--third-color) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#e04856",endColorstr=var(--error-color),GradientType=1);
}
